  }
  
  .gslideOutLeft {
    animation: gslideOutLeft 0.3s ease;
  }
  
  .gslideInLeft {
    animation: gslideInLeft 0.3s ease;
  }
  
  .gslideOutRight {
    animation: gslideOutRight 0.3s ease;
  }
  
  .gslideInRight {
    animation: gslideInRight 0.3s ease;
  }
  
  .gzoomIn {
    animation: gzoomIn 0.5s ease;
  }
  
