  @keyframes lightboxLoader {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes gfadeIn {
    from {
      opacity: 0;
    }
  
    to {
      opacity: 1;
    }
  }
  
  @keyframes gfadeOut {
    from {
      opacity: 1;
    }
  
    to {
      opacity: 0;
    }
  }
  
  @keyframes gslideInLeft {
    from {
      opacity: 0;
      transform: translate3d(-60%, 0, 0);
    }
  
    to {
      visibility: visible;
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }
  
  @keyframes gslideOutLeft {
    from {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0, 0, 0);
    }
  
    to {
      transform: translate3d(-60%, 0, 0);
      opacity: 0;
      visibility: hidden;
    }
  }
  
  @keyframes gslideInRight {
    from {
      opacity: 0;
      visibility: visible;
      transform: translate3d(60%, 0, 0);
    }
  
    to {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }
  
  @keyframes gslideOutRight {
    from {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0, 0, 0);
    }
  
    to {
      transform: translate3d(60%, 0, 0);
      opacity: 0;
    }
  }
  
  @keyframes gzoomIn {
    from {
      opacity: 0;
      transform: scale3d(0.3, 0.3, 0.3);
    }
  
    to {
      opacity: 1;
    }
  }
  
  @keyframes gzoomOut {
    from {
      opacity: 1;
    }
  
    50% {
      opacity: 0;
      transform: scale3d(0.3, 0.3, 0.3);
    }
  
    to {
      opacity: 0;
    }
  }
  
