  /*Checkbox*/
  .form-checkbox:checked {
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z'/%3e%3c/svg%3e");
    border-color: transparent;
    background-color: currentColor;
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
  }
  .form-checkbox {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: inline-block;
    vertical-align: middle;
    background-origin: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    flex-shrink: 0;
    background-color: #fff;
    border-width: 1px;
  }
  .toggle-checkbox:checked {
    --tw-border-opacity:1;
    border-color:rgba(239, 240, 254, var(--tw-border-opacity));
  }
  [dir="ltr"] .toggle-checkbox:checked {
    right:0px;
  }
  [dir="rtl"] .toggle-checkbox:checked {
    left:0px;
  }
  .toggle-checkbox:checked + .toggle-label {
    background-color: #1f2937;
  }
  .dark .toggle-checkbox:checked + .toggle-label {
    background-color: #D1D5DB;
  }
  
  /*Brands slider*/
  .brands-image {
    margin-right:auto;
    margin-left:auto;
    filter: gray;
    -webkit-filter: grayscale(100%);
    opacity: .8;
    transition: all 0.3s ease;
  }
  .brands-image:hover {
    filter: grayscale(0%);
    opacity: 1;
  }
  
  /*Preloader*/
  .preloader {
    z-index: 60;
  }
  .loaded-success .preloader {
    visibility: hidden ;
    transition: all 1s ease-in-out;
  }
  .animate-spin-slow {
    animation: spin 3s linear infinite;
  }
  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
  
    to {
      transform: rotate(360deg);
    }
  }
  
  /*Simple scrollbar*/
  .scrollbars::-webkit-scrollbar {
    width: 6px;
  }
  .scrollbars::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px transparent;
    border-radius: 10px;
  }
  .scrollbars::-webkit-scrollbar-thumb {
    background: transparent;
    border-radius: 10px;
  }
  .scrollbars.show::-webkit-scrollbar-thumb, .scrollbars:hover::-webkit-scrollbar-thumb {
    background: #777;
  }
  
  /*Form validation*/
  .has-success .text-help {
    --tw-text-opacity:1;
    color:rgba(16, 185, 129, var(--tw-text-opacity));
  }
  .has-success input, .has-success textarea, .has-success select, .has-success .tagify {
    border-bottom-width:2px;
    border-bottom-color: #10B981;
  }
  .has-success input.form-checkbox {
    border-width:1px;
    --tw-border-opacity:1;
    border-color:rgba(16, 185, 129, var(--tw-border-opacity));
  }
  .has-danger .text-help {
    --tw-text-opacity:1;
    color:rgba(239, 68, 68, var(--tw-text-opacity));
  }
  .has-danger input, .has-danger textarea, .has-danger select, .has-danger .tagify {
    border-bottom-width:2px;
    border-bottom-color: #EF4444;
  }
  .has-danger input.form-checkbox {
    border-width:1px;
    --tw-border-opacity:1;
    border-color:rgba(239, 68, 68, var(--tw-border-opacity));
  }
  .form-group .text-help {
    padding-left:0px;
    padding-right:0px;
    padding-top:0.5rem;
    padding-bottom:0.5rem;
  }
  
  /*task check*/
  .task-check input[type=checkbox]:checked + span {
    text-decoration:line-through;
  }
  
  /* Dragula  */
  .gu-mirror {
    position: fixed ;
    margin: 0 ;
    list-style: none;
    z-index: 9999 ;
    opacity: 0.8;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
    filter: alpha(opacity=80);
  }
  .gu-hide {
    display: none ;
  }
  .gu-unselectable {
    -webkit-user-select: none ;
    -moz-user-select: none ;
    user-select: none ;
  }
  .gu-transit {
    opacity: 0.2;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=20)";
    filter: alpha(opacity=20);
  }
  .drop-item {
    touch-action: none;
  }
  /* Microtip */
  [aria-label][role~="tooltip"] {
    position: relative;
  }
  [aria-label][role~="tooltip"]::before,
  [aria-label][role~="tooltip"]::after {
    transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    will-change: transform;
    opacity: 0;
    pointer-events: none;
    transition: all var(--microtip-transition-duration, .18s) var(--microtip-transition-easing, ease-in-out) var(--microtip-transition-delay, 0s);
    position: absolute;
    box-sizing: border-box;
    z-index: 10;
    transform-origin: top;
  }
  
  [aria-label][role~="tooltip"]::before {
    background-size: 100% auto ;
    content: "";
  }
  
  [aria-label][role~="tooltip"]::after {
    background: rgba(17, 17, 17, .9);
    border-radius: 4px;
    color: #ffffff;
    content: attr(aria-label);
    font-size: var(--microtip-font-size, 13px);
    font-weight: var(--microtip-font-weight, normal);
    text-transform: var(--microtip-text-transform, none);
    padding: .5em 1em;
    white-space: nowrap;
    box-sizing: content-box;
  }
  [aria-label][role~="tooltip"]:hover::before,
  [aria-label][role~="tooltip"]:hover::after,
  [aria-label][role~="tooltip"]:focus::before,
  [aria-label][role~="tooltip"]:focus::after {
    opacity: 1;
    pointer-events: auto;
